body {
  background-color: #fefefe;
  font-family: 'Source Sans Pro', sans-serif;
  color: #657b83;
  min-width: 420px; }

a {
  color: #268bd2;
  text-decoration: none; }

a:hover {
  color: #6c71c4; }

html body a.no-link {
  color: #fefefe !important; }

html body a.no-link:hover {
  color: #fefefe !important; }

pre, code, kbd {
  white-space: pre-wrap;
  /* Since CSS 2.1 */
  white-space: -moz-pre-wrap;
  /* Mozilla, since 1999 */
  white-space: -pre-wrap;
  /* Opera 4-6 */
  white-space: -o-pre-wrap;
  /* Opera 7 */
  word-wrap: break-word;
  /* Internet Explorer 5.5+ */ }

form {
  margin-bottom: inherit; }

#footer {
  font-size: 0.6em;
  font-family: 'Source Code Pro', monospace; }

hr {
  margin: 2em 0; }

td {
  border: none; }

/* Utilities */
.smaller {
  font-size: smaller; }

.center {
  text-align: center; }

/* Foreground Colors */
.fg-brown {
  color: #b58900; }

.fg-orange {
  color: #cb4b16; }

.fg-blue {
  color: #268bd2; }

.fg-green {
  color: #859900; }

.fg-red {
  color: #dc322f; }

/* Background Colors */
.bg-blue {
  background-color: #268bd2;
  color: #fefefe; }

.bg-green {
  background-color: #859900;
  color: #fefefe; }

.bg-red {
  background-color: #dc322f;
  color: #fefefe; }

.bg-orange {
  background-color: #cb4b16;
  color: #fefefe; }

.bg-brown {
  background-color: #b58900;
  color: #fefefe; }

.bg-default {
  background-color: #efefef; }

/* Cards */
.card {
  padding: 1em;
  border-radius: 10px;
  margin-bottom: 1.5em;
  /* Card Customizations */ }
  .card h1, .card h2, .card h3, .card h4, .card h5 {
    margin: 0; }
  .card.bg-blue, .card.bg-green, .card.bg-red, .card.bg-orange, .card.bg-brown {
    padding: 1.5em; }
  .card.bg-orange a, .card.bg-brown a {
    color: #ffd15d; }
  .card.bg-red a, .card.bg-blue a, .card.bg-green a {
    color: navajowhite; }
  .card.bg-blue a:hover, .card.bg-red a:hover, .card.bg-green a:hover, .card.bg-orange a:hover, .card.bg-brown a:hover {
    color: #f9b54c; }
  .card p {
    margin: 0; }

/* Header and Navigation */
#header {
  padding-top: 10px;
  padding-bottom: 13px;
  margin-bottom: 2em;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease; }
  #header .nav {
    display: inline-block; }
  #header a {
    font-weight: normal;
    color: #859900;
    vertical-align: top; }
  #header #menu-toggle {
    line-height: 0.5em;
    display: none;
    color: #859900; }
  #header #brand {
    display: inline-block;
    font-size: 1.3em;
    font-weight: bold;
    width: 315px;
    margin-top: 6px; }
    #header #brand img {
      margin-right: 8px; }
  #header nav {
    float: right;
    position: relative;
    top: 12px; }
  #header ul {
    margin-bottom: 0; }
  #header li {
    list-style: none;
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    min-width: 50px;
    text-align: center;
    border-radius: 5px; }
    #header li i {
      color: #859900; }
    #header li:last-child {
      margin-right: inherit; }
    #header li:hover, #header li.active {
      background-color: rgba(133, 153, 0, 0.1); }
  #header .navbar-icons li {
    padding: 0; }
  #header.solid {
    background-color: #859900;
    color: #fefefe; }
    #header.solid a {
      color: #fefefe; }
    #header.solid li:hover, #header.solid li.active {
      background-color: rgba(255, 255, 255, 0.1); }
    #header.solid li i {
      color: #fefefe; }
    #header.solid #menu-toggle {
      color: #fefefe; }

.brand {
  font-family: 'Source Code Pro', monospace;
  font-weight: bold; }

#background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.container > #main > h1 {
  margin-top: 0; }

#main h1, #main h2, #main h3, #main h4, #main h5 {
  margin-top: 1em; }

#main .image {
  border-radius: 1em;
  border: 1px solid #93a1a1;
  margin: 1em; }

#main .card.bg-default:nth-child(3n-2) {
  background-color: rgba(38, 139, 210, 0.08); }
  #main .card.bg-default:nth-child(3n-2) h4 a {
    color: #268bd2; }

#main .card.bg-default:nth-child(3n-1) {
  background-color: rgba(203, 75, 22, 0.08); }
  #main .card.bg-default:nth-child(3n-1) h4 a {
    color: #cb4b16; }

#main .card.bg-default:nth-child(3n) {
  background-color: rgba(133, 153, 0, 0.08); }
  #main .card.bg-default:nth-child(3n) h4 a {
    color: #859900; }

#main code {
  display: block; }

#main p > code {
  display: inline; }

#main p > code.header {
  display: block; }

#main h2 {
  font-size: 1.8em;
  border-bottom: 1px dashed #859900;
  padding-bottom: 10px; }

#main h3 {
  font-size: 1.55em; }

#main h4 {
  font-size: 1.2em; }

#main .item h4 {
  font-size: 1.2em;
  margin-top: 0; }

#main .item h5 {
  font-size: 1em;
  margin-bottom: 1em; }

#main .item p {
  font-size: 0.7em; }

#main .item-tag {
  font-size: 0.7em;
  float: left;
  margin-right: 8px;
  margin-bottom: 8px;
  padding-left: 8px;
  padding-right: 8px;
  border-radius: 5px;
  background-color: RGBA(0, 0, 0, 0.1); }

#main #related > h5 {
  font-size: 1.2em; }

#main #related .item {
  background: none;
  margin: 0 0 1em 0;
  padding: 0; }
  #main #related .item h4 {
    font-size: 1em; }
  #main #related .item h5 {
    font-size: 0.8em; }
  #main #related .item p {
    float: none;
    margin: 0;
    font-size: 0.7em;
    font-style: italic; }

#main #related .item-tag {
  display: none; }

#main .logo {
  width: 2em;
  height: 2em;
  vertical-align: bottom; }

#main #intro {
  background: url(/image/logo-bg.svg) no-repeat center;
  background-size: 45vh;
  min-width: 45vh;
  min-height: 45vh;
  text-align: center; }
  #main #intro h1 {
    padding-top: 14vh;
    font-size: 3em;
    font-weight: bold;
    color: #859900;
    margin-bottom: 4px; }
  #main #intro h3 {
    font-size: 1.2em;
    color: #859900;
    margin-bottom: 2em; }
  #main #intro h2 {
    font-size: 1.5em;
    color: #859900; }

/* Responsiveness */
@media (max-width: 1195px) {
  #header nav .nav-name {
    display: none; } }

@media (max-width: 1000px) {
  #header #menu-toggle {
    display: inline-block;
    position: absolute;
    right: 10px;
    top: 15px; }
  #header #brand {
    display: block; }
  #header nav {
    float: none;
    display: none; }
    #header nav .nav-name {
      display: inline; }
  #header li {
    text-align: left;
    display: block; }
  #header .navbar-icons {
    display: block; }
    #header .navbar-icons li {
      display: inline-block;
      padding-left: 1em; } }
